<template>
    <section>
        <template>
            <div class="modal my-popups right fade show" id="manageProjectModal" role="dialog" style="display: block;">
                <div class="modal-dialog" role="document">
                    <div class="modal-content">
                        <div class="modal-header mheader-user">
                            <button type="button" class="close" @click="$emit('CloseSearchItem')"><i class="fa fa-times"
                                    aria-hidden="true"></i></button>
                            <h4 class="modal-title" id="myModalLabel">{{ $t('SearchItems') }}</h4>
                        </div>
                        <div class="modal-body">
                            <div class="row">
                                <div class="listing">
                                    <div class="form--group">
                                        <div class="custom-control p-0 custom-radio mr-4 ml-4 custom-control-inline">
                                            <input type="radio" v-model="SearchValue" value="Barcode" class="mr-2"
                                                @change="ChangeSearchValue('Barcode')" /> {{ $t('SearchbyBarcode') }}
                                        </div>
                                        <div class="custom-control p-0 custom-radio mr-4 ml-4 custom-control-inline">
                                            <input type="radio" v-model="SearchValue" value="searchproduct" class="mr-2"
                                                @change="ChangeSearchValue('searchproduct')" />{{ $t('CASearchbyAssetType')
                                                }}
                                        </div>
                                    </div>
                                </div>

                                <div class="barcode-boxes col-12 mt-3" v-if="showFirst">
                                    <Form ref="barcode">
                                        <div
                                            class="col-sm-12 col-md-12 col-lg-12 col-xl-12 searchpanel tablehead search float-left p-0">
                                            <div
                                                class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left pt-2 p-0">
                                                <div class="col-md-12">
                                                    <div class="row mt-0">
                                                        <div class="col-md-12 pl-0">
                                                            <div class="form-group mb-3">
                                                                <label><span id="">{{ $t('SearchbyBarcode') }}
                                                                        {{ $t('Barcode') }}/{{ $t('SerialNumber') }}/{{
                                                                            $t('RFID')
                                                                        }}:</span></label><span class="text-danger">*</span>

                                                                <Field name="BarCode" class="w-100"
                                                                    :rules="{ 'required': true }" v-slot="{ errors }">
                                                                    <input name="BarCode" placeholder="Enter Barcode"
                                                                        v-model="txtBarcode" @keyup="searchfilter()"
                                                                        :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                        type="text">
                                                                    <span v-if="errors[0]" name="BarCode"
                                                                        class="invalid-feedback d-block">{{
                                                                            errors[0] }}</span>
                                                                </Field>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="col-md-12 pl-0">

                                            <div class="form-group col-12 col-sm-9 col-md-9 col-lg-9 float-left pl-0">

                                                <label>{{ $t('Location') }}<span class="text-danger">*</span></label>
                                                <Field name="Location" class="w-100" :rules="{ 'required': true }"
                                                    v-slot="{ errors }">
                                                    <tree-view v-model="TreeValue" :options="options"
                                                        v-on:input="BindItemTypeByUserId(TreeValue)"
                                                        placeholder="Select Location" :show-count="true" :flat="true"
                                                        v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }">
                                                    </tree-view>

                                                    <span v-if="errors[0]" name="Location"
                                                        class="invalid-feedback d-block">{{
                                                            errors[0]
                                                        }}</span>
                                                </Field>

                                            </div>

                                            <div
                                                class="col-12 col-sm-3 col-md-3 col-lg-3 float-left pl-md-0 pr-md-0 mt-0 mt-md-4 mt-sm-4">
                                                <div
                                                    class="search-btm-btn background-0 text-left form-check-inline m-0 w-100">
                                                    <a href="javascript:;" title="Search" v-on:click="searchByBarcode"
                                                        class="btn btn-success mr-1 px-4 w-50"><em
                                                            class="fa fa-search text-white"></em></a>
                                                    <a title="Clear" href="javascript:;"
                                                        class="btn btn-danger clearsearchtext px-4 w-50"><em
                                                            class="fa fa-refresh"></em></a>
                                                </div>
                                            </div>
                                        </div>
                                    </Form>
                                </div>

                                <Form ref="product" class="product-type col-12 mt-3" v-if="!showFirst">
                                    <div id="itemsearch" class="col-md-12">
                                        <div class="row">
                                            <div class="col-md-6 pl-0">
                                                <div class="form-group">
                                                    <label>{{ $t('ProductType') }}<span class="text-danger">*</span></label>
                                                    <Field name="Product Type" class="w-100" :rules="{ 'required': true }"
                                                        v-slot="{ errors }">
                                                        <select name="Product Type" v-model="txtProductType"
                                                            v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }"
                                                            @change="getProductNameList">
                                                            <option value="" selected>{{ $t('Select') }}</option>
                                                            <option v-for="(item, itemIndex) in productTypeList"
                                                                :key="itemIndex" :value="item.ASSETID">{{ item.ASSETTYPE }}
                                                            </option>
                                                        </select>

                                                        <span v-if="errors[0]" name="Product Type"
                                                            class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                    </Field>
                                                </div>
                                            </div>
                                            <div class="col-md-6">
                                                <div class="form-group">
                                                    <div class="form-group">
                                                        <label>{{ $t('ProductName') }}<span
                                                                class="text-danger"></span></label>

                                                        <Field name="Product Name" class="w-100"
                                                            :rules="{ 'required': true }" v-slot="{ errors }">
                                                            <select name="Product Name" v-model="txtProductName"
                                                                v-bind:class="{ 'form-control w-100': true, 'is-invalid': errors[0] }"
                                                                @change="getProductItemNameList(txtProductName)">
                                                                <option value="" selected>{{ $t('Select') }}</option>
                                                                <option v-for="(item, itemIndex) in productNameList"
                                                                    :key="itemIndex" :value="item.Value">{{ item.Text }}
                                                                </option>
                                                            </select>
                                                            <span v-if="errors[0]" name="Product Name"
                                                                class="invalid-feedback d-block">{{ errors[0] }}</span>
                                                        </Field>


                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                        <div class="row">
                                            <div class="col-md-6 pl-0">

                                                <label> {{ $t('SelectItem') }}</label>
                                                <div class="d-flex" style="align-items: flex-start;">
                                                    <Field name="Products" :rules="{ 'required': false }" label="ItemName"
                                                        v-slot="{ errors }" style="width:85%">
                                                        <div class="input-group mb-3" style="position: relative;">
                                                            <input name="Products" id="Products" type="text"
                                                                v-bind:class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                v-model="productUniqueName" value="iselectVal" />

                                                            <a @click="itemListOnClick" class="btn float-left"
                                                                href="javascript:;"
                                                                style="background-color: #fff;color: #303030;border-color: #abb5bf;border-left: none;"><em
                                                                    class="dropdown-toggle float-right"></em></a>
                                                            <span v-if="errors[0]" name="Products"
                                                                class="invalid-feedback d-block">{{
                                                                    errors[0] }}</span>

                                                            <div class="unique_dynamicdatalist">
                                                                <ul v-for="(item, index) in productItemNameList"
                                                                    @click="setProductItemName(item)" style="z-index: auto;"
                                                                    :key="index">
                                                                    <li v-if="showProductDropdown"
                                                                        style="text-transform: capitalize;"
                                                                        :value="item.Value">{{ item.Name }}</li>
                                                                </ul>
                                                            </div>

                                                        </div>
                                                    </Field>
                                                    <div class="input-group mb-3 w-auto" style="position: relative;">

                                                        <div class="input-group-append">
                                                            <span
                                                                class="input-group-text  border-0 bg-transparent p-0 pl-2">
                                                                <a id="Searchassest" href="javascript:;"
                                                                    @click="searchByProduct()"
                                                                    class="btn btn-outline-success float-left" title=""
                                                                    data-toggle-tooltip="tooltip"
                                                                    data-original-title="Click here to search"><i
                                                                        class="fa fa-search"></i></a>
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>

                                            </div>

                                        </div>


                                    </div>
                                </Form>
                                <div class="row">
                                    <div class="col-12 mb-2 mt-2">
                                        <div class="mr-2 d-inline-block">
                                            <span class="legend-Progress colorboxsquare"></span>Container Products
                                        </div>
                                    </div>
                                </div>

                                <div class="listing">
                                    <div class="table-responsive table-fix-header">
                                        <table id="tableuniquename" class="table table-bordered dt-responsive nowrap my-0">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th><span style="max-width: 7px;">#</span></th>
                                                    <th><span>{{ $t('ProductName') }}</span></th>
                                                    <th><span>{{ $t('AvailableQuantity') }}</span></th>
                                                    <th><span>{{ $t('PendingCheckOutRequests') }}</span></th>
                                                    <th><span>{{ $t('RequestQuantity') }}</span></th>
                                                    <th><span>{{ $t('Compartment')
                                                    }}</span></th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr class=" showassetcataloguegroup low-bar"
                                                    v-for="(item, index) in searchProductData" :key="index">
                                                    <td class="textaligncenter align-middle item-data">
                                                        <!-- :disabled="item.IsEnableTerms ? item.IsEnableTerms == 1 : null" -->
                                                        <div class="custom-control custom-checkbox"><input type="checkbox"
                                                                id="chk_0" class="custom-control-input"><label
                                                                class="custom-control-label universal-custom-control-label pt-1"
                                                                for="chk_0"></label></div>


                                                    </td>
                                                    <td class="align-middle ad item-name">{{ item.Name }}</td>

                                                    <td class="textalignright align-middle">
                                                        {{ item.AvailableItems
                                                        }}
                                                    </td>
                                                    <td class="textalignright align-middle">
                                                        {{
                                                            item.PendingAvailableQuantity }}
                                                    </td>
                                                    <td class="align-middle" v-if="item.CreateUniqueNameListing == true">
                                                        <input disabled type="text"
                                                            v-model="item.PendingTotalAvailableItems"
                                                            class="form-control txtitems textfield txtAssetQuentity textalignright align-middle ">
                                                        <span></span>
                                                    </td>
                                                    <td class="align-middle" v-else>
                                                        <input type="text" v-model="item.PendingTotalAvailableItems"
                                                            class="form-control txtitems textfield txtAssetQuentity textalignright align-middle ">
                                                        <span></span>
                                                    </td>

                                                    <td class="align-middle">
                                                        <span>N/A</span>
                                                    </td>

                                                </tr>

                                                <tr v-if="NoRecordfound">
                                                    <td colspan="8" class="text-center text-danger no-record">{{
                                                        $t('NoRecordfound') }}</td>
                                                </tr>

                                            </tbody>

                                        </table>
                                    </div>
                                    <div class="col-lg-12 text-right mt-3 float-left">
                                        <a href="javascript:;" id="btnSave" class="btn btn-success " title="Save"
                                            v-on:click="addToList"><em class="fa fa-save pr-2"></em>{{
                                                $t('AddToList') }} </a>
                                        <a v-on:click="$emit('CloseSearchItem')" class="btn btn-danger m-1"><em
                                                class="fa fa-close pr-2"></em>{{
                                                    $t('Close') }}</a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </section>
</template>
<script>

import DataService from '../../service/DataService';
import Treeselect from '@riophae/vue-treeselect'
import '@riophae/vue-treeselect/dist/vue-treeselect.css'
export default {
    components: {
        'tree-view': Treeselect
    },

    props: {

        TreeValue: {
            type: Number
        },
    },

    data() {
        return {
            LocationData: [],
            productNameList: [],
            productTypeList: [],
            NoRecordfound: true,
            txtLocation: '',
            showFirst: true,
            SearchValue: 'searchproduct',
            UserId: null,
            options: [],
            txtProductName: null,
            txtProductType: null,
            productItemNameList: [],
            showProductDropdown: false,
            productUniqueName: '',
            productUniqueId: null,
            searchProductData: [],
            addListData: []
        }
    },
    created() {
        var vm = this;
        vm.UserId = vm.GetUserInfo.ID;
        vm.ChangeSearchValue(vm.SearchValue);
        vm.getProductTypeList();
    },

    methods: {

        GetLocationCheckOutByuserId: async function () {

            var vm = this;
            let locationId = null;
            var url = `reqForm=transfer`
            await DataService.GetLocationByuserId(url).then(response => {
                vm.FindalLocationArray = [];
                this.TreeValue = response.data.data[0].COMPANY_LOCATION_ID;
                response.data.data.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });

                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, response.data.data)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });
                vm.options = vm.FindalLocationArray;
            });
        },

        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
            childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
            return retrunArray;
        },

        searchByProduct: async function () {

            debugger;
            var vm = this;
            vm.$refs.product.validate().then(result => {
                if (result) {

                    var vm = this;
                    let currentDate = new Date();
                    let today = new Date()
                    let currentTime = new Date();
                    today.setDate(currentDate.getDate() + 1);
                    currentDate = vm.$options.filters.formatDate(currentDate);
                    var endDate = vm.$options.filters.formatDate(today);
                    var startTime = vm.$options.filters.formatTimee(currentTime);
                    var endTime = vm.$options.filters.formatTimee('12:00');
                    var url = `id=${vm.txtProductType}&isView=1&barcode=&assetType=${vm.txtProductName}&assetUniqueId=${vm.productUniqueId}&selfCheckout=&reqFrom=assetrequisition&isFor=&isuniqueSearch=&issueTo=${vm.UserId}&assignedFrom=${currentDate}&assignedTill=${endDate}&locationId=${vm.TreeValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&startTime=${startTime}&endTime=${endTime}&uniqueIds=${vm.productUniqueId}&catalogueIds=`;

                    DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                        vm.searchProductData = response.data;
                    });

                }
            })
        },

        searchByBarcode: async function () {
            var vm = this;
            vm.$refs.barcode.validate().then(result => {
                if (result) {
                    console.log(result)
                }
            })
        },

        addToList: function () {
            debugger;

            var vm = this;
            let currentDate = new Date();
            let today = new Date()
            let currentTime = new Date();
            today.setDate(currentDate.getDate() + 1);
            currentDate = vm.$options.filters.formatDate(currentDate);
            var endDate = vm.$options.filters.formatDate(today);
            var startTime = vm.$options.filters.formatTimee(currentTime);
            var endTime = vm.$options.filters.formatTimee('12:00 AM');

            var url = `id=${vm.txtProductType}&isView=1&barcode=&assetType=${vm.txtProductName}&assetUniqueId=&selfCheckout=&reqFrom=assetrequisition&isFor=&isuniqueSearch=&issueTo=${vm.UserId}&assignedFrom=${currentDate}&assignedTill=${endDate}&locationId=${vm.TreeValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&startTime=${startTime}&endTime=${endTime}&uniqueIds=${vm.productUniqueId}&catalogueIds=`;
            DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                debugger;
                vm.addListData = response.data;

            });


        },

        ChangeSearchValue: function (ChangeValue) {

            if (ChangeValue == 'searchproduct') {
                this.showFirst = false;
            } else {
                this.showFirst = true;
            }
        },



        setProductItemName: function (item) {
            var vm = this;
            vm.productUniqueId = item.Value;
            vm.productUniqueName = item.Name;
            vm.showProductDropdown = false;

        },

        itemListOnClick: function () {
            this.showProductDropdown = true;

        },

        getProductTypeList: function () {

            var vm = this;
            var url = `type=&issueTo=${this.UserId}&locationId=${this.TreeValue}&requestType=&requestFrom=assetrequisition`;
            DataService.AssignGetAssetType(url).then(response => {
                vm.productTypeList = response.data;

            });


        },

        getProductNameList: function () {

            var vm = this;
            var url = `id=${vm.txtProductType}&isView=&barcode=&assetType=&assetUniqueId=&selfCheckout=&reqFrom=assetrequisition&isFor=&isuniqueSearch=&issueTo=${vm.UserId}&assignedFrom=&assignedTill=&startTime=&endTime=&locationId=${vm.TreeValue}&RFID=&pageSize=&pageNum=&assignedFromDateTime=&assignedTillDateTime=&uniqueIds=&catalogueIds=`;
            DataService.GetCompanyAssestCatalogsDetalis(url).then(response => {
                vm.productNameList = response.data.Result;

            });

        },

        getProductItemNameList: function () {

            var vm = this;
            var url = `assetCatalogId=${this.txtProductName}&name=&issueTo=${this.UserId}&locationId=${this.TreeValue}&requestFrom=assetrequisition&assetType=`;
            DataService.GetUniqueAssetItems(url).then(response => {
                vm.productItemNameList = response.data;
                vm.NoRecordfound = false
            });

        },




    }
}
</script>

<style>
.vue-treeselect__control {
    border: none;
    height: 24px;
    padding: 0;
}

.vue-treeselect--searchable .vue-treeselect__input-container {
    padding: 0 !important;
}

.vue-treeselect__placeholder {
    color: #303030 !important;
}

.vue-treeselect__control-arrow {
    color: #000;
}

.vue-treeselect__placeholder {
    line-height: 24px;
}
</style>